<template>
  <div class="login_container">
    <div class="login_box">
      <!--  -->
      <div class="avator_box">
        <img src="https://z3.ax1x.com/2021/04/25/cxl5kj.png" alt="" />
      </div>
      <!--  -->
      <el-form
        label-width="0px"
        class="login_form"
        ref="loginFormRef"
        :rules="loginFormRules"
        :model="loginForm"
      >
        <!--  -->
        <el-form-item prop="loginName">
          <el-input
            prefix-icon="iconfont icon-user"
            v-model="loginForm.loginName"
          ></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item prop="loginPassword">
          <el-input
            type="password"
            prefix-icon="iconfont icon-3702mima"
            v-model="loginForm.loginPassword"
          ></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item class="btns">
          <el-button
            type="primary"
            @click="login"
            v-loading.fullscreen.lock="fullscreenLoading"
            ></el-button
          >
          <el-button type="info" @click="restLoginForm"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: "Login",
  data() {
    return {
      fullscreenLoading: false,
      // 
      loginForm: {
        loginName: "",
        loginPassword: "",
      },
      //
      loginFormRules: {
        //
        loginName: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 2,
            max: 20,
            message: " 2  20 ",
            trigger: "blur",
          },
        ],
        // 
        loginPassword: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 4,
            max: 15,
            message: " 4  15 ",
            trigger: "blur",
          },
        ],
      },
    };
  },
  methods: {
    //
    restLoginForm() {
      this.loginForm.loginName = "";
      this.loginForm.loginPassword = "";
    },
    // 
    login() {
      this.$refs.loginFormRef.validate(async (valid) => {
        if (!valid) return;
        this.fullscreenLoading = true;
        const { data: res } = await this.$http.post("login", this.loginForm);
        // console.log(res);
        if (res.err_code != 200){
          this.$message.error("")
          this.fullscreenLoading = false
        }
        if (res.role.roleId == 1) {
          this.fullscreenLoading = false
          this.$notify({
            title: "",
            message: "",
            type: "success",
          });
          //1.tokensessionStorage
          //1.1API
          //1.2tokentokensessionStorage
          window.sessionStorage.setItem("token", res.token);
          //2./home
          this.$router.push("/home");
        } else if (res.role.roleId == 0) {
          this.fullscreenLoading = false;
          // this.$message.success(res.role.stuName + "");
          this.$notify({
            title: "" + res.role.stuName,
            message: "",
            type: "success",
          });
          window.sessionStorage.setItem("token", res.token);
          //
          window.localStorage.setItem(
            "stu_enrollmentNumber",
            res.role.stuEnrollmentNumber
          );
          window.localStorage.setItem("stu_name", res.role.stuName);
          this.$router.push("/stuhome");
        }
      });
    },
  },
};
</script>

<style lang="less" scoped>
.login_container {
  background-image: url(https://z3.ax1x.com/2021/04/28/gihrZj.jpg);
  background-repeat: no-repeat;
  background-size: 100%;
  height: 100vh;
}

.login_box {
  width: 450px;
  height: 300px;
  background-color: grey;
  border-radius: 10px;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  margin: auto;
}

.avator_box {
  height: 130px;
  width: 130px;
  border: 1px solid #eee;
  border-radius: 50%;
  padding: 10px;
  box-shadow: 0 0 10px #ddd;
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
}

img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #eee;
}

.login_form {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 0 20px;
  box-sizing: border-box;
}

.btns {
  display: flex;
  justify-content: flex-end;
}
</style>
